	mixins: [MescrollMixin], // 使用mixin (在main.js注册全局组件)
	components: {
		foot,
		pop,
		latelyPhotoList
	},
	data() {
		return {
			photoList: [{
					id: 0,
					shopName: "布达佩斯大饭店（东大门店）",
